.steps{width:100%;height:60px;overflow:hidden}.label{position:relative;top:-53px;left:-40px;font-size:11px}.form{width:335px;position:relative;top:584px;left:164px;display:flex;flex-wrap:wrap}.form .label-check{margin-top:1px}.form button{margin-top:15px}.fake{width:415px;height:560px;background:url(/portfolio_steps/static/media/fake.3b43f90c.jpg);box-shadow:0px 0px 12px 0px rgba(0,0,0,0.3);margin:50px auto;background-position:-10px -13px;border-radius:10px;position:relative}.fake .arrow-1{position:absolute;top:547px}.first-line{width:100px;height:3px;background-color:#22c1c6;position:absolute;top:40px;left:90px}.MuiStepLabel-active .label,.MuiStepLabel-completed .label{color:#22c1c6}.input{cursor:pointer;margin:3px 6px 0 0}

.arrow-1{cursor:pointer;position:relative;width:80px;height:50px;margin:20px}.arrow-1 div{position:relative;top:20px;width:68px;height:10px;background-color:#22c1c6;box-shadow:-5px 10px 19px rgba(0,0,0,0.3);left:0;display:block}.arrow-1 div::after{content:'';position:absolute;width:25px;height:10px;top:-5px;right:-8px;background-color:#22c1c6;-webkit-transform:rotate(45deg);transform:rotate(45deg)}.arrow-1 div::before{content:'';position:absolute;width:25px;height:10px;top:5px;right:-8px;background-color:#22c1c6;box-shadow:-5px 10px 19px rgba(0,0,0,0.3);-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}.arrow-1{-webkit-animation:arrow-1 5s linear infinite;animation:arrow-1 5s linear infinite}@-webkit-keyframes arrow-1{0%{left:0;opacity:.2}50%{left:40px;opacity:1}100%{left:0;opacity:.2}}@keyframes arrow-1{0%{left:0;opacity:.2}50%{left:40px;opacity:1}100%{left:0;opacity:.2}}

.menu{position:absolute;left:0;top:0;border:1px solid #dbdbdb;height:100vh;z-index:1;background:white}.menu__points{margin:0;color:#777573;margin-top:23px;min-width:50px}.menu__points-wrap{margin-top:-5px}.menu__points-disabled{display:none}.menu__collapse{display:flex;width:127px;margin-top:17px;-webkit-transition:width 0.3s;transition:width 0.3s}.menu__icons-wrap{width:37px}.menu__collapse-collapsed{width:20px}.menu__scaled{-webkit-transform:scaleX(-1);transform:scaleX(-1)}.menu__collapse-button{float:right;margin:3px;-webkit-transition:-webkit-transform 0.1s;transition:-webkit-transform 0.1s;transition:transform 0.1s;transition:transform 0.1s, -webkit-transform 0.1s}.menu__collapse-button:hover{cursor:pointer}.menu__icon-wrapper-collapsed{border-right:2px solid #e98036;width:133px;text-align:left;padding:15px 1px 0 11px;height:25px;margin-top:3px}.menu__icon-wrapper:nth-child(1){border-right:2px solid #e98036;width:30px}.menu__icon-wrapper{display:flex;justify-content:center;align-items:center;margin:13px 6px 0px 6px;padding:1px;width:28px;height:26px}.MuiButton-containedPrimary{width:170px}

